import {preview} from '../../common/js/common.js'

export default{
	name:'Upload',
	data(){
		return{
			currentIndex:0,
			classify:['景点','美食','网红','打卡'],
			uploadedImg:[]
		}
	},
	methods:{
		// 点击分类
		tapItem(index){
			this.currentIndex = index
		},
		// 点击上传图片
		uploadImg(){
			uni.chooseImage({
				count:8,
				sizeType:['original','compressed'],
				sourceType:['album'],
				success:res => {
					console.log(res)
					this.uploadedImg.push(...res.tempFilePaths)
				}
			})
		},
		// 删除图片
		delImage(index){
			this.uploadedImg.splice(index,1)
		},
		// 预览图片
		previewImg(index){
			preview(index,this.uploadedImg)
			.then(res => {
				console.log('预览成功')
			})
			.catch(err => {
				console.log('预览失败')
			})
		}
	}
}
